import React, { useEffect, useState, useCallback } from 'react';
import './Match.scss';
import Confetti from 'react-confetti';
import { useNavigate } from 'react-router-dom';
import { useWindowSize } from 'react-use';
import anime from '../../components/anime';
import match from '../../assets/match.svg';
// import heartRedBordered from '../../assets/icons/heartRedBordered.svg'
import { useTelegram } from '../../hooks/useTelegram';
import photo7 from '../../assets/images/7.jpg';

const user1 = {
    name: 'User 1',
    photo: photo7
};

const user2 = {
    name: 'User 2',
    photo: photo7
};
function Match() {
    const { tg } = useTelegram();
    const [showConfetti, setShowConfetti] = useState(false);
    const { width, height } = useWindowSize();
    const navigate = useNavigate();

    const onBack = useCallback(() => {
        navigate('/swipes');
    }, [])

    useEffect(() => {
        tg.onEvent('backButtonClicked', onBack)
        return () => {
            tg.offEvent('backButtonClicked', onBack)
        }
    }, [onBack])

    useEffect(() => {
        if (!tg.BackButton.isVisible) {
            tg.BackButton.show();
        }

        if (tg.SettingsButton.isVisible) {
            tg.SettingsButton.hide();
        }
    }, [tg])

    useEffect(() => {
        const timeout = setTimeout(() => {
            const text = document.querySelector('.greeting');
            text.innerHTML = text.innerText.replace(/\S/g, "<span>$&</span>");
            anime.timeline({ loop: false })
                .add({
                    targets: '.greeting span',
                    translateX: [40, 0],
                    translateZ: 0,
                    opacity: [0, 0.5],
                    easing: "easeOutExpo",
                    delay: (el, i) => 500 + 30 * i
                });
            anime.timeline({ loop: false })
                .add({
                    targets: '.greeting ',
                    opacity: [0, 1],
                    easing: "easeOutExpo",
                    delay: (el, i) => 500 + 30 * i
                });
            document.querySelector('.user1').classList.add('animate', 'user1');
            document.querySelector('.user2').classList.add('animate', 'user2');
            // document.querySelector('.heart').classList.add('animate', 'heart');
            setShowConfetti(true);
        }, 1000);
        return () => clearTimeout(timeout);
    }, []);

    return (
        <div className="match-container">
            <img src={match} alt="logoAlt" className="logoClass" />
            <div className="greeting">Новая пара!</div>
            {showConfetti &&
                <Confetti
                    numberOfPieces={60}
                    gravity={0.1}
                    recycle={false}
                    opacity={0.8}
                    confettiSource={{ x: width / 2, y: 180, w: 30, h: 30 }}
                // confettiSource={{ x: width / 2, y: 0, w: width, h: 0 }}

                />
            }
            <div className="user user1">
                <img src={user1.photo} alt={user1.name} className="user-photo" />
            </div>
            {/* <img src={heartRedBordered} alt={user1.name} className="heart" /> */}
            <div className="user user2">
                <img src={user2.photo} alt={user2.name} className="user-photo" />
            </div>
            <div className="buttons">
                <button className="chat-button">Начать чат</button>
                <button className="continue-button">Продолжить поиск</button>
            </div>
        </div>
    );
}

export default Match;
