import React, { useEffect, useState, useCallback } from 'react';
import { useTelegram } from '../../hooks/useTelegram';
import { useNavigate } from 'react-router-dom';
import { addReaction, getReactionsByToUserId } from '../../router/apiServiceFetch';
import './Styles.scss';
import CardItem from './CardItem';

const MatchWithCards = () => {
    const navigate = useNavigate();
    const [likes, setLikes] = useState([]);
    const { tg, user } = useTelegram();

    // const [user] = useState({ id: 6256940687 });

    const onBack = useCallback(() => {
        navigate('/profile');
    }, [navigate]);

    useEffect(() => {
        if (!tg.BackButton.isVisible) {
            tg.BackButton.show();
        }
    }, [tg])

    useEffect(() => {
        tg.onEvent('backButtonClicked', onBack);
        return () => {
            tg.offEvent('backButtonClicked', onBack);
        };
    }, [tg, onBack]);


    const handleReaction = async (reactionType, fromUserId) => {
        try {
            await addReaction({
                fromUserId: user.id,
                toUserId: fromUserId,
                isLike: reactionType === 'like',
                isDisLike: reactionType === 'dislike',
                isSuper: reactionType === 'super',
                likeViewDate: new Date().toISOString(),
                mutualViewDate: new Date().toISOString()
            });

            // Fetch updated likes list after reaction
            fetchLikes();
        } catch (error) {
            console.error(`Error adding/updating ${reactionType} reaction:`, error);
        }
    };

    const handleLike = (fromUserId) => {
        handleReaction('like', fromUserId);
    };

    const handleDislike = (fromUserId) => {
        handleReaction('dislike', fromUserId);
    };

    const handleWrite = (fromUserId) => {
        const telegramLink = `tg://user?id=${fromUserId}`;
        window.open(telegramLink, '_blank');
    };

    const fetchLikes = async () => {
        try {
            const likesResponse = await getReactionsByToUserId(user.id, 1, 10, true, false); // Adjust the parameters as needed
            console.log(likesResponse);
            setLikes(likesResponse);
        } catch (error) {
            console.error('Error fetching likes:', error);
        }
    };

    useEffect(() => {
        fetchLikes();
    }, [user.id]);

    return (
        <div className="likes-with-cards">
            <h2>Matches</h2>
            <div className="cards-container">
                {likes?.map((likeUser) => (
                    <CardItem
                        key={likeUser.fromUserId}
                        userId={likeUser.fromUserId}
                        isMatch={true}
                        onWrite={() => handleWrite(likeUser.fromUserId)}
                    />
                ))}
            </div>
        </div>
    );
};

export default MatchWithCards;
