import React from 'react';
import { Routes, Route, useLocation } from 'react-router-dom';
import { AnimatePresence, motion } from 'framer-motion';
import Home from './pages/Home/Home';
import RegistrationForm from './pages/Registration/RegistrationForm';
import PhotoUpload from './pages/PhotoUpload/PhotoUpload';
import VerificationPage from './pages/Verification/VerificationPage';
import SelfieUpload from './pages/Selfie/SelfieUpload';
import WaitingForModeration from './pages/Moderation/WaitingForModeration';
import UserProfile from './pages/Profile/UserProfile';
import Swipes from './pages/Swipe/SwipeCards';
import TechnicalWorks from './pages/TechnicalWorks/TechnicalWorks';
import Match from './pages/Match/Match';
import Likes from './pages/Likes/Likes';
import Mutuals from './pages/Likes/Mutuals';

const pageTransition = {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
    transition: { duration: 0.3 }
};

const RoutesComponent = () => {
    const location = useLocation();

    return (
        <AnimatePresence mode="wait">
            <Routes location={location} key={location.pathname}>
                <Route
                    path="/"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <Home />
                        </motion.div>
                    }
                />
                <Route
                    path="/tech"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <TechnicalWorks />
                        </motion.div>
                    }
                />
                <Route
                    path="/register"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <RegistrationForm />
                        </motion.div>
                    }
                />
                <Route
                    path="/photo-upload"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <PhotoUpload />
                        </motion.div>
                    }
                />
                <Route
                    path="/verification"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <VerificationPage />
                        </motion.div>
                    }
                />
                <Route
                    path="/selfie-upload"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <SelfieUpload />
                        </motion.div>
                    }
                />
                <Route
                    path="/waiting-for-moderation"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <WaitingForModeration />
                        </motion.div>
                    }
                />
                <Route
                    path="/profile"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <UserProfile />
                        </motion.div>
                    }
                />
                <Route
                    path="/swipes"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <Swipes />
                        </motion.div>
                    }
                />
                <Route
                    path="/match"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <Match />
                        </motion.div>
                    }
                />
                <Route
                    path="/likes"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <Likes />
                        </motion.div>
                    }
                />
                <Route
                    path="/mutuals"
                    element={
                        <motion.div
                            initial="initial"
                            animate="animate"
                            exit="exit"
                            variants={pageTransition}
                        >
                            <Mutuals />
                        </motion.div>
                    }
                />
            </Routes>
        </AnimatePresence>
    );
};

export default RoutesComponent;
