import React, { useState, useEffect, useCallback } from 'react';
import { VerifyIcon, LocationIcon } from "../../components/GetIcons.js";
import { useTranslation } from '../../contexts/TranslationContext';
import { useSpring, animated } from '@react-spring/web';
import { useNavigate } from 'react-router-dom';
import './UserProfile.scss';
import jerm from '../../assets/jerm.svg';
import heart_small from '../../assets/icons/heart-small.svg';
import hearts_small from '../../assets/icons/hearts-small.svg';
import { useTelegram } from '../../hooks/useTelegram';
import GetZodiacSign from '../../components/GetZodiacSign.js';
import CalculateAge from '../../components/CalculateAge.js';
import { getUser, getPhotosByUser, updatePhoto, updateUser, getMutualCount, getLikeCount } from '../../router/apiServiceFetch.js';
import Popup from '../../components/Popup';

const UserProfile = () => {
    const { t } = useTranslation();
    const [userData, setUserData] = useState({
        id: null,
        fullName: '',
        birthday: '',
        profession: '',
        city: '',
        description: '',
        photos: []
    });
    const [age, setAge] = useState();
    const [fillColor, setFillColor] = useState('#333'); // Default to dark
    const [likeCount, setLikeCount] = useState(0);
    const [mutualCount, setMutualCount] = useState(0);
    const likes = useSpring({ from: { number: 0 }, number: likeCount, delay: 200 });
    const matches = useSpring({ from: { number: 0 }, number: mutualCount, delay: 400 });

    const navigate = useNavigate();
    const { tg, user } = useTelegram();

    // const [user] = useState({ id: 6256940687 });
    const [isPopupOpen, setIsPopupOpen] = useState(false);
    const [editData, setEditData] = useState({
        profession: '',
        description: ''
    });

    const onMatch = useCallback(() => {
        navigate('/match');
    }, [navigate]);

    const onBack = useCallback(() => {
        navigate('/search');
    }, [navigate]);

    const loadUserData = async () => {
        try {
            const userId = user.id;
            const userData = await getUser(userId);
            if (userData) {
                setUserData(userData);
                setAge(CalculateAge(new Date(userData.birthday)));
                if (localStorage.getItem('theme') === 'light') {
                    setFillColor('#333');
                } else {
                    setFillColor('#fff');
                }
            }
        } catch (error) {
            console.error('Error fetching user data:', error);
        }
    };

    const loadUserPhotos = async () => {
        try {
            const userId = user.id;
            const photosData = await getPhotosByUser(userId);
            if (photosData?.length > 0)
                setUserData(prevUserData => ({
                    ...prevUserData,
                    photos: photosData?.map(photo => ({
                        ...photo,
                        photo: photo.base64Data // Ensure to use base64Data here
                    }))
                }));
        } catch (error) {
            console.error('Error fetching user photos:', error);
        }
    };

    const loadLikesCount = async () => {
        try {
            const userId = user.id;
            const likeCountData = await getLikeCount(userId);
            setLikeCount(Number(likeCountData.likeCount) || 0);
        } catch (error) {
            console.error('Error fetching like count:', error);
        }
    };

    const loadMutualCount = async () => {
        try {
            const userId = user.id;
            const mutualCountData = await getMutualCount(userId);
            setMutualCount(Number(mutualCountData.mutualCount) || 0);
        } catch (error) {
            console.error('Error fetching mutual count:', error);
        }
    };

    useEffect(() => {
        loadUserData();
        loadUserPhotos();
        loadLikesCount();
        loadMutualCount();
    }, [user]);

    useEffect(() => {
        if (!tg.BackButton.isVisible) {
            tg.BackButton.show();
        }

        if (!tg.SettingsButton.isVisible) {
            tg.SettingsButton.show();
        }
    }, [tg]);

    useEffect(() => {
        tg.onEvent('backButtonClicked', onBack);
        return () => {
            tg.offEvent('backButtonClicked', onBack);
        };
    }, [onBack, tg]);

    useEffect(() => {
        tg.onEvent('settingsButtonClicked', onMatch);
        return () => {
            tg.offEvent('settingsButtonClicked', onMatch);
        };
    }, [onMatch, tg]);

    const handleFileUpload = (e, index) => {
        const file = e.target.files[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = async () => {
                const base64String = reader.result.split(',')[1]; // Получение base64 части строки
                const fileExtension = `.${file.name.split('.').pop()}`; // Получение расширения файла
                const newPhotos = [...userData.photos];
                newPhotos[index].photo = base64String;

                // Обновление фото на сервере
                try {
                    const id = newPhotos[index].id;
                    const userId = userData.userId;
                    await updatePhoto({ id, userId, photoBase64: base64String, imageExtension: fileExtension });
                    setUserData(prevUserData => ({
                        ...prevUserData,
                        photos: newPhotos
                    }));
                } catch (error) {
                    console.error('Error updating photo:', error);
                }
            };
            reader.readAsDataURL(file);
        }
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setEditData(prevEditData => ({
            ...prevEditData,
            [name]: value
        }));
    };

    const handleSaveChanges = async () => {
        try {
            await updateUser(userData.userId, {
                profession: editData.profession,
                description: editData.description
            });
            setUserData(prevUserData => ({
                ...prevUserData,
                profession: editData.profession,
                description: editData.description
            }));
            setIsPopupOpen(false);
        } catch (error) {
            console.error('Error updating user data:', error);
            alert('Failed to update profile');
        }
    };

    const openPopup = () => {
        setEditData({
            profession: userData.profession,
            description: userData.description
        });
        setIsPopupOpen(true);
    };

    const closePopup = () => {
        setIsPopupOpen(false);
    };

    return (
        <div className="user-profile">
            <img src={jerm} alt="logoAlt" className="logoClass" />
            <div className="profile-content">
                <div className="photo-grid">
                    {userData?.photos?.map((photo, index) => (
                        <div key={index} className={`photo-box photo-box-${index + 1}`}>
                            <label htmlFor={`upload-${index}`} className="upload-label">
                                {photo?.photo ? (
                                    <img src={`data:image/jpeg;base64,${photo.photo}`} alt={`Upload Preview ${index + 1}`} className="photo-preview" />
                                ) : (
                                    <span>+</span>
                                )}
                            </label>
                            <input
                                id={`upload-${index}`}
                                type="file"
                                accept="image/*"
                                onChange={(e) => handleFileUpload(e, index)}
                                className="upload-input"
                            />
                        </div>
                    ))}
                </div>
                <div className="counters">
                    <div className="counter" onClick={() => navigate('/likes')}>
                        <animated.div>{likes.number.to(n => n.toFixed(0))}</animated.div>
                        <img src={heart_small} alt={'heartAlt'} className="heartClass" />
                    </div>
                    <div className="counter" onClick={() => navigate('/mutuals')}>
                        <animated.div>{matches.number.to(n => n.toFixed(0))}</animated.div>
                        <img src={hearts_small} alt={'heartsAlt'} className="heartClass" />
                    </div>
                </div>
                <div className="profile-info">
                    <div className='info_top'>
                        <h3>{GetZodiacSign(userData.birthday, fillColor)}{userData.fullName}, {age} <VerifyIcon className='verify' /></h3>
                        <p><LocationIcon fillColor={fillColor} /> {userData.city}</p>
                    </div>
                    <p className='title'>{t('profession')}: </p>
                    <p className='text'>{userData.profession}</p>
                    <p className='title'>{t('description')}: </p>
                    <p className='text'>{userData.description}</p>
                    <button onClick={openPopup} className="edit-button">Change Info</button>
                </div>
            </div>
            <Popup
                isOpen={isPopupOpen}
                onClose={closePopup}
                onSave={handleSaveChanges}
                profession={editData.profession}
                description={editData.description}
                handleInputChange={handleInputChange}
            />
        </div>
    );
}

export default UserProfile;
